:root {
  --primary-color: #e0c521;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.console-container {
  display: grid;
  margin-bottom: 20px;
}

.console {
  border: 1px solid #ccc;
  padding: 10px;
  margin-bottom: 10px;
  width: 80%;
}

.form-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center; /* Menggunakan justify-content untuk membuat tombol berada di tengah */
  margin-bottom: 20px;
}

.form-container input {
  margin-right: 12px; /* Memberikan jarak antara input dan tombol */
  padding: 5px;
}

.items-container {
  display: list-item;
  flex-direction: column;
  width: 80%;
  border: 1px solid #ccc;
  padding: 10px;
  margin-bottom: 20px;
}

.items-container h3 {
  margin-bottom: 10px;
}

.items-container ul {
  list-style-type: none;
  padding: 0;
}

.items-container li {
  border: 2px solid #ccc;
  padding: 10px;
  margin-bottom: 10px;
}

.items-container li p {
  margin: 0;
}

.container {
  text-align: center;
  color: white;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: rgb(19, 19, 52);
  color: #fff;
}

input {
  margin: 10px;
}

.btn {
  padding: 0.3rem 0.4rem;
  font-size: 0.5rem;
  text-align: center;
  border: 0;
  outline: none;
  border-radius: 5px;
  width: 120px;
  margin-left: 10px;
  box-shadow: 1px -3px 52px -5px rgba(0, 0, 0, 0.77);
  cursor: pointer;
}

/* Tombol */
.button-85 {
  padding: 0.6em 8em;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-85:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing-button-85 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

@keyframes glowing-button-85 {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.button-85:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #222;
  left: 0;
  top: 0;
  border-radius: 10px;
}

.button-85:hover {
  background-color: #0056b3;
}

.heading {
  text-align: center;
}
